                <a :href="link" class="btn btn-success mb-2 col-5" v-if="(link != '' && total != 0 )"> Pagar Mercadopago </a> <br>
                <button class="btn btn-danger mb-2 col-3" @click="link= '' " v-if="(link != '' && total != 0 )"> Cancelar </button><br v-if="link != ''">
                <button class="btn btn-danger col-3" href="#" data-dismiss="modal">
                  <i class="fas fa-times fa-fw"></i>
                  Cerrar
                </button>
              </div>
            </div>
          </div>
        </div>

            <!-- The Modal -->
            <div class="modal" id="modalValidaciones">
              <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content" style="border: black solid">

                  <!-- Modal Header -->
                  <div class="modal-header justify-content-center">
                    <h4 class="modal-title">Error</h4>
                  </div>
                  <!-- Modal body -->
                  <div class="modal-header justify-content-center">
                    {{msj}}
                  </div>

                  <!-- Modal footer -->
                  <div class="modal-header justify-content-center"> 
                    <button type="button" class="btn btn-danger" @click="hideModal()">Close</button>
                  </div>

                </div>
              </div>
            </div>
      </div>
    </div>
  </div> 



 <button @click="ver()" id="tam" style="display:none" ></button>

    <div class="btn-finalizar btn btn-success"  v-bind:style="{'right': tamPantalla + 'px'}" id="finalizar" v-if="(mostrar)">
    <a data-toggle="modal" data-target="#compra">Finalizar compra 
              <span class="badge badge-pill badge-danger">{{contador}}</span>
            </a>
  </div> 

  <div class="btn-whatsapp">
    <a href="https://wa.me/541130226715?text=Hola!" target="_blank">
    <img src="../../public/img/whatsapp.png" alt="">
    </a>
  </div>
  
</div> 
  <div id="ocultar" style="background-color: #fee440; position: absolute; z-index: 99999; width: 100%; height: 100%; top: 0px; left: 0px;"><img src="img/logo.png" style="position: absolute; top: calc(50% - 150px); left: calc(50% - 150px); height: 300px; width: 300px;"></div>

</div> 

</template>

<script>

import { mapState, mapActions , mapGetters} from "vuex";

export default {
  name: 'Home',
  data(){
    return {
      tamPantalla: 0,
      mostrar: false,
      link: '',
      contador:0,
      comprar: [],
      nroItem: 1,
      loading: false,
      total: 0,
      contadorbanner: 0,
      provincia: '',
      localidad: '',
      CodigoPostal:'',
      direccion: '',
      entrega:'',
      envio:0,
      cliente: {
        nombre:'',
        areaTelefono: '',
        telefono: '',
        direccion:'',
        codPostal: '',
      },
      msj: ''
    }
  },
  methods: {
    ...mapActions(['getProductos','addProductos','delProductos','putProductos','getBanners','getLocalidades']),
    contadorBanner(){
      this.contadorbanner++
      console.log(contadorbanner);

    },
    agregar(item){
      let i = {}
      i.nombre = item.nombre
      i.numero = item.numero
      i.precio = parseFloat(item.precio)
      i.id = this.nroItem
      i.uid = item.uid
      i.tipo = item.tipo

      this.comprar.push(i);
      this.contador = this.comprar.length
      this.nroItem++
      this.total+= i.precio
      this.link=''
      this.loading = false
    },
    eliminar(id){
      this.comprar = this.comprar.filter(doc => {
          return doc.id != id
        })
      this.total = 0
      this.comprar.forEach((p) =>{
        this.total+= p.precio
      });
      this.contador = this.comprar.length
      this.link=''
      this.loading = false
    },
    ver(){
      var tam = $(window).width()/2
      if(this.contador > 0 && (tam <= 496)){
        this.mostrar = true
      }else{
        this.mostrar = false
      }
    },
    clickMP(){
      // debugger
      if(this.validar()){
        this.loading = true
        let enviarMP = this.comprar.slice()
        if(this.entrega == 'envio'){
          let item = this.productos.find(p => p.uid == 'WD1kGcv3J37KCUO1i8h5')
          
           let i = {}
            i.nombre = item.nombre
            i.numero = item.numero
            i.precio = parseFloat(item.precio)
            i.id = 99
            i.uid = item.uid
            i.tipo = 'Envio'
  
            enviarMP.push(i);
        }
        this.cliente.direccion = ''
        this.cliente.direccion = this.direccion + ', '+ this.localidad + ', '+ this.provincia;
  
        this.axios.post('/mp',{'items':enviarMP, 'payer': this.cliente})
        .then(res => {
          console.log(res);
          // debugger
          this.link = res.data.init_point;
          console.log(this.link);
          this.loading = false
        })
        .catch(error => {
          console.log(error.response);
          this.loading = false
        })
      } else {
        // alert('Faltan datos del Cliente')
      }
    },
    closeMenu(){
      var navbar = $(".navbar-collapse");
      navbar.removeClass('show');
    },
    enviomonto(){
      if(this.entrega == 'envio'){
        this.envio = parseInt(this.precioEnvio)
      }
      else {
        this.envio = 0
      }
      this.link=''
      this.loading = false
    },
    validar(){
      var data = this.cliente
      if(this.entrega == ''){
        this.msj = 'Debe seleccionar una Formar de Entrega'
        $('#modalValidaciones').show()
        return false
      }
      if(data.nombre == ''){
        this.msj = 'Debe ingresar su Nombre'
        $('#modalValidaciones').show()
        return false
      }
      if(data.areaTelefono == ''){
        this.msj = 'Debe ingresar el Codigo de Area'
        $('#modalValidaciones').show()
        return false
      }
      if(data.telefono == ''){
        this.msj = 'Debe ingresar su Nro de Telefono'
        $('#modalValidaciones').show()
        return false
      }
      if(this.entrega == 'envio'){
        if(this.localidad == '' || this.localidad == undefined){
        this.msj = 'Debe seleccionar una Localidad'
        $('#modalValidaciones').show()
          return false
        }
        if(this.provincia == '' || this.provincia == undefined){
        this.msj = 'Debe seleccionar una Provincia'
        $('#modalValidaciones').show()
          return false
        }
        if(data.codPostal == '' || data.codPostal == [] || data.codPostal == undefined){
        this.msj = 'Debe seleccionar un Cod. Postal'
        $('#modalValidaciones').show()
          return false
        }
        if(this.direccion == '' || this.direccion == undefined){
        this.msj = 'Debe ingresar su Direccin'
        $('#modalValidaciones').show()
          return false
        }
      }
      return true
    },
    hideModal(){
      $('#modalValidaciones').hide()
    },
   isNumber: function(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault();;
      } else {
        return true;
      }
    }
  },
  computed: {
    ...mapState(['productos','ListaLanzamiento','ListaRemeres','ListaTazas','ListaAccesorios','banners','Localidades', 'Provincias','precioEnvio'])
  },
  created() {
    this.tamPantalla = $(window).width()/2 -100 ;
    this.getBanners();
    console.log('created');
    // this.getLocalidades();
    // this.getProductos();
  },
  beforeCreate() {
    console.log('beforeCreate');
  },
  mounted() {
    console.log('mounted');
    // this.getBanners();
    $(window).resize(function(){
      //aqui el codigo que se ejecutara cuando se redimencione la ventana
      this.tamPantalla = $(window).width()/2 -100
      $("#finalizar").css("right", this.tamPantalla +"px");
      $("#tam").click(); 
    });

    setTimeout(() => {
      $('#ocultar').fadeOut();
       $('#mostrar').fadeIn();
      $('body').css('overflow-y','scroll')
        }, 3000);
  },
  beforeMount() {
    this.getLocalidades();  
  },
}
</script>

