import Vue from 'vue'
import Vuex from 'vuex'
import router from '@/router'

import {db, autn} from '@/main'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    productos: [],
    ListaLanzamiento: [],
    ListaRemeres: [],
    ListaTazas: [],
    ventas: [],
    ListaAccesorios: [],
    usuario: {
      uid: '', 
      email: ''
    },
    error : '',
    banners: [],
    ListaBannerAll: [],
    ListaBannerPagina: [],
    ListaBannerCelular: [],
    Localidades: [],
    Provincias: [],
    precioEnvio: ''
  },
  mutations: {
    setProductos(state, payload){
      let startTime = new Date();

      if(payload.tipo == 'get'){
        state.productos = payload.productos.filter(p => p.eliminado != true)
      }else if(payload.tipo == 'add'){
        console.log(payload.producto);
        state.productos.push(payload.producto)
        console.log(payload.producto);
      }else if(payload.tipo == 'delete'){
        state.productos = state.productos.filter(doc => {
          return doc.uid != payload.id
        })
      }
      else if(payload.tipo == 'update'){
        state.productos = state.productos.filter(doc => {
          if(doc.uid == payload.payload.uid){
            doc.nombre = payload.payload.nombre
            doc.descripcion = payload.payload.descripcion
            doc.tipo  = payload.payload.tipo
            doc.talles = payload.payload.talles
            doc.modelos  = payload.payload.modelos
            doc.lanzamiento = payload.payload.lanzamiento
            doc.precio = payload.payload.precio
            doc.url = payload.payload.url
            doc.nombreFile = payload.payload.nombreFile
          }
          return doc
        })
      }
      state.productos = state.productos.sort(function (a, b) {
        if (a.nombre.toLowerCase() > b.nombre.toLowerCase()) { return  1; }
        if (a.nombre.toLowerCase() < b.nombre.toLowerCase()) { return -1; }
        return 0;
      });
      state.precioEnvio = state.productos.filter(e => e.uid == 'WD1kGcv3J37KCUO1i8h5')[0].precio

      var pList = []
      state.productos.forEach((p) =>{
       if(p.lanzamiento == true ){
         pList.push(p)
       }
      });
      state.ListaLanzamiento = pList

      pList = []
      state.productos.forEach((p) =>{
        if(p.tipo == 'Remeras' ){
          pList.push(p)
        }
      });
      state.ListaRemeres = pList

      pList = []
      state.productos.filter(p => p.uid != 'WD1kGcv3J37KCUO1i8h5').forEach((p) =>{
      if(p.tipo == 'Tazas'){
        pList.push(p)
      }
      });
      state.ListaTazas = pList

      pList = []
      state.productos.forEach((p) =>{
      if(p.tipo == 'Accesorios'){
        pList.push(p)
      }
      });
      state.ListaAccesorios = pList

      if(state.productos.filter.length > 0){
        setTimeout(() => {
          $('.navbar-nav li a').click(function(e){
            // Evitamos que se haga el scroll
            e.preventDefault();
      
            // Capturamos el ancla
            var ancla = $(this).attr('href');
            console.log(ancla);
      
            // Le quitamos el numeral # para solo quedarnos con el nombre de la seccin
            // ancla = ancla.substring(1);
            // console.log(ancla);
      
            // Obtenemos la posicin de la seccin
            var position = $(ancla).position();
            console.log(ancla);
      
            var nav = $('#mainNav').outerHeight()
      
            // Hacemos el efecto scroll y le restamos algunos pixeles, en este caso 180
            $('html, body').animate({scrollTop: (position.top - nav -10)}, 0); 
          })
        }, 100);
      }

      let endTime = new Date();
      console.log('Hora Iniciio setProductos'+ startTime);
      console.log('Tiempo setProductos '+(endTime - startTime)/1000 +' sec');
      },
    setVentas(state, payload){
      if(payload.tipo == 'get'){
        state.ventas = payload.ventas
      }

      state.ventas = state.ventas.sort(function (a, b) {
        if (a.IdOrden > b.IdOrden) { return  1; }
        if (a.IdOrden < b.IdOrden) { return -1; }
        return 0;
      });

      },
    setUsuario(state, payload){
      if(payload != null){
        state.usuario = payload
      }else{
        state.usuario = {
          uid: '', 
          email: ''
        }
      }
    },
    setError(state, payload){
      state.error = payload
    },
    setBanners(state, payload){
      console.log('setBanners');
      if(payload.tipo == 'get'){
        state.banners = payload.banners
      }else if(payload.tipo == 'add'){
        console.log(payload.banner);
        state.banners.push(payload.banner)
        console.log(payload.banner);
      }else if(payload.tipo == 'delete'){
        state.banners = state.banners.filter(doc => {
          return doc.uid != payload.id
        })
      }
      state.banners = state.banners.sort(function (a, b) {
        if (a.nombre.toLowerCase() > b.nombre.toLowerCase()) { return  1; }
        if (a.nombre.toLowerCase() < b.nombre.toLowerCase()) { return -1; }
        return 0;
      });

      for (let index = 0; index < state.banners.length; index++) {
        state.banners[index].id = index
      }

      var pList = []
      state.banners.forEach((p) =>{
        if(p.tipo == 'Celular' && p.activo == true){
          pList.push(p)
        }
      });
      state.ListaBannerCelular = pList

      pList = []
      state.banners.forEach((p) =>{
      if(p.tipo == 'Pagina' && p.activo == true ){
        pList.push(p)
      }
      });
      state.ListaBannerPagina = pList

      pList = []
      state.banners.forEach((p) =>{
      if(p.activo == true){
        pList.push(p)
      }
      });
      state.ListaBannerAll = pList
      if(state.banners.length > 0){
        setTimeout(() => {
          $('#carouselExampleInterval').carousel()
        }, 100);
      }
      console.log('Set Fin');
    },
    setLocalidades(state, payload){
      state.Localidades = payload
    },
    setProvincias(state, payload){
      state.Provincias = payload
    }
  },
  actions: {
    getProductos({commit}){
      let startTime = new Date();
      // Busco en Firebase la colleccion
      const productos = []
      db.collection('productosNew').get()
      .then(snapshot => {
        
        snapshot.forEach( doc => {
          let p = doc.data();
          p.uid = doc.id
          productos.push(p)
        })
        // console.log(productos);
        let endTime = new Date();
        console.log('Hora Inicio getProductos '+ startTime);
        console.log('Tiempo getProductos '+(endTime-startTime)/1000 +' sec');
        // Cargo las Varibles Globales
        commit('setProductos', {tipo:'get', productos:productos})
      })
      .catch((err)=>{
        console.log(err);
      })
    },
    async addProducto({commit}, payload){
      await db.collection('productosNew').add(
        payload
      )
      .then(doc => {
          let p = payload;
          console.log(p);
          p.uid = doc.id
        // Cargo las Varibles Globales
        console.log(p);
        commit('setProductos', {tipo:'add', producto:p})
      })
      .catch((err)=>{
        console.log(err);
      })
    },
    async putProducto({commit}, payload){
      // await db.collection('productosNew').add(
        await db.collection('productosNew').doc(payload.uid).update({
          nombre : payload.nombre,
          descripcion : payload.descripcion,
          tipo  : payload.tipo,
          talles : payload.talles,
          modelos  : payload.modelos,
          lanzamiento : payload.lanzamiento,
          precio : payload.precio,
          url : payload.url,
          nombreFile : payload.nombreFile
        }).then(function() {
          commit('setProductos',{tipo:'update' , payload})
      })
      .catch(function(error) {
          console.error("Error updating document: ", error);
      });
    },
    delProducto({commit}, payload){
      db.collection('productosNew').doc(payload.uid).update({
        eliminado : true
      }).then(function() {
        commit('setProductos',{tipo:'delete' , payload})
    })
    .catch(function(error) {
        console.error("Error updating document: ", error);
    });
    },
    async ingresoUsuario({commit}, payload){
      var user = null;
      // Login
      commit('setError','');
      var res = await autn.signInWithEmailAndPassword(payload.email, payload.pass)
      .then(res=>{
        console.log(res);
        commit('setUsuario', {uid: res.user.uid, email: res.user.email});
      })
      .catch(err=>{
        // Error de Creacion Usuario
        commit('setError', err.message)
      })
    },
    cerrarSesion({commit}){
      // Cierra la sesion y lleva a ingreso
      autn.signOut()
      .then(()=>{
        commit('setUsuario', null)
      })
    },
    getVentas({commit}){
      // Busco en Firebase la colleccion
      const productos = []
      db.collection('ventas').get()
      .then(snapshot => {
        
        snapshot.forEach( doc => {
          let p = doc.data();
          p.uid = doc.id
          productos.push(p)
        })
        console.log(productos);
        // Cargo las Varibles Globales
        commit('setVentas', {tipo:'get', ventas:productos})
      })
      .catch((err)=>{
        console.log(err);
      })
    },
    async getBanners({commit}){
      let startTime = new Date();
      // Busco en Firebase la colleccion
      const banners = []
      await db.collection('banners').get()
      .then(snapshot => {
        
        snapshot.forEach( doc => {
          let p = doc.data();
          p.uid = doc.id
          banners.push(p)
        })
        // console.log('getBanners');
        let endTime = new Date();
        console.log('Hora Inicio getBanners '+ startTime);
        console.log('Tiempo getBanners '+(endTime - startTime)/1000 +' sec');
        // Cargo las Varibles Globales
        commit('setBanners', {tipo:'get', banners:banners})
      })
      .catch((err)=>{
        console.log(err);
      })
    },
    async addBanner({commit}, payload){
      await db.collection('banners').add(
        payload
      )
      .then(doc => {
          let p = payload;
          console.log(p);
          p.uid = doc.id
        // Cargo las Varibles Globales
        console.log(p);
        commit('setBanners', {tipo:'add', banner:p})
      })
      .catch((err)=>{
        console.log(err);
      })
    },
    delBanner({commit}, payload){
      db.collection('banners').doc(payload).delete()
      .then(() => {
        commit('setBanners', {tipo:'delete', id:payload})
      })
      .catch((err)=>{
        console.log(err);
      })
    },
    async getLocalidades({commit}){
      // Busco en Firebase la colleccion
      fetch('localidades.json')
        .then( res => res.json())
        .then(datos => {
          const students = [
            {
              name: 'Krunal',
              age: 26
            },
            {
              name: 'Ankit',
              age: 25
            },
            {
              name: 'Krunal',
              age: 28
            }
          ]
          const uniqueArr = [... new Set(datos.map(data => data.Provincia))]
          commit('setProvincias', uniqueArr)
          commit('setLocalidades', datos)
        });
    },
  }
})
